* {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

nav{
	display: flex;
	width: 100%;
	margin: auto;
	max-width: 1000px;
	justify-content: space-between;
	text-align: center;
}
nav a {
	padding: 1rem 2rem 1.15rem;
  text-transform: uppercase;
  cursor: pointer;
  color: #ebebeb;
	min-width: 80px;
	margin: auto;
}

nav a:hover {
  background-image: url(/portfoliooo/static/media/SL_022420_28400_02.e4059529.jpg);
  background-size: 100% 100%;
  border-radius: 35%;
  color: #ffffff;
  -webkit-animation: spring 300ms ease-out;
          animation: spring 300ms ease-out;
  text-shadow: 0 -1px 0 #fc72b7;
	font-weight: bold;
}
nav a:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

@-webkit-keyframes spring {
  15% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.2, 1.1);
  }
  40% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(0.95, 0.95);
  }
  75% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.05, 1);
  }
  100% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1, 1);
  }
}

@keyframes spring {
  15% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.2, 1.1);
  }
  40% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(0.95, 0.95);
  }
  75% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.05, 1);
  }
  100% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1, 1);
  }
}

.shameless-plug{
  position: absolute;
  bottom: 10px;
  right: 0;
  padding: 8px 20px;
  color: #ccc;
  text-decoration: none;
}

span {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-80%, -60%);
          transform: translate(-80%, -60%);
  display: block;
  color: #ff4ed9;
  font-size: 124px;
  letter-spacing: 8px;
  cursor: pointer;
}

.lead {
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-10%, 10%);
	        transform: translate(-10%, 10%);
	display: block;
	color: #73a4ff;
	font-size: 30px;
	letter-spacing: 8px;
	cursor: pointer;	
}

span::before {
  content: "VIVIANNA COWAN";
  position: absolute;
  color: transparent;
  background-image: repeating-linear-gradient(
    45deg,
    transparent 0,
    transparent 2px,
    white 2px,
    white 4px
  );
  -webkit-background-clip: text;
  top: 0px;
  left: 0;
  z-index: -1;
  transition: 1s;
}

span::after {
  content: "VIVIANNA COWAN";
  position: absolute;
  color: transparent;
  background-image: repeating-linear-gradient(
    135deg,
    transparent 0,
    transparent 2px,
    white 2px,
    white 4px
  );
  -webkit-background-clip: text;
  top: 0px;
  left: 0px;
  transition: 1s;
}

span:hover:before {
  top: 10px;
  left: 10px;
}

span:hover:after {
  top: -10px;
  left: -10px;
} 



.container  {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
