  font-family: 'Merriweather', serif;
}

h1 {
  font-weight: 900;
  text-align: center;
}

h3 {
  font-weight: 300;
}

.flap {
  width: 100%;
  height: 100%;
}

.flap::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 50%;
  background: url("https://pbs.twimg.com/profile_images/1347260174176710658/2GfSZ1i__400x400.jpg") white;
  background-position: 100px;
  background-repeat: no-repeat;
  transition: 1s;
}

.flap::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 50%;
  right: 0;
  background: url("https://pbs.twimg.com/profile_images/1347260174176710658/2GfSZ1i__400x400.jpg") white;
  background-position: -200px;
  background-repeat: no-repeat;
  transition: 1s;
}

.container:hover .flap::after {
  -webkit-transform: translatex(300px);
          transform: translatex(300px);
}

.container:hover .flap::before{
  -webkit-transform: translatex(-300px);
          transform: translatex(-300px);
}
:root {
	font-family: 'Chakra Petch', sans-serif;
	font-family: 'IBM Plex Mono', monospace;
}
:root {
	--colorShadeA: rgb(0, 0, 0);
	--colorShadeB: rgb(58, 58, 58);
	--colorShadeC: rgb(109, 109, 109);
	--colorShadeD: rgb(158, 158, 158);
	--colorShadeE: rgb(212, 212, 212);
  }

  button {
	margin-top: 15px;
	position: relative;
	display: inline-block;
	cursor: pointer;
	outline: none;
	border: 0;
	vertical-align: middle;
	text-decoration: none;
	font-size: 1.5rem;
	  color:rgb(0, 0, 0);
	  color:var(--colorShadeA);
	font-weight: 700;
	text-transform: uppercase;
	font-family: inherit;
  }
  
  button.big-button {
	 padding: 1em 2em;
	 border: 2px solid rgb(0, 0, 0);
	 border: 2px solid var(--colorShadeA);
	border-radius: 1em;
	background: rgb(212, 212, 212);
	background: var(--colorShadeE);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
	 transition: all 175ms cubic-bezier(0, 0, 1, 1);
  }
  button.big-button::before {
	position: absolute;
	color:black;
	content: '';
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgb(109, 109, 109);
	background: var(--colorShadeC);
	border-radius: inherit;
	  box-shadow: 0 0 0 2px rgb(58, 58, 58), 0 0.75em 0 0 rgb(0, 0, 0);
	  box-shadow: 0 0 0 2px var(--colorShadeB), 0 0.75em 0 0 var(--colorShadeA);
   -webkit-transform: translate3d(0, 0.75em, -1em);
           transform: translate3d(0, 0.75em, -1em);
	   transition: all 175ms cubic-bezier(0, 0, 1, 1);
  }
  
  
  button.big-button:hover {
	background: rgb(158, 158, 158);
	background: var(--colorShadeD);
	-webkit-transform: translate(0, 0.375em);
	        transform: translate(0, 0.375em);
  }
  
  button.big-button:hover::before {
	-webkit-transform: translate3d(0, 0.75em, -1em);
	        transform: translate3d(0, 0.75em, -1em);
  }
  
  button.big-button:active {
			  -webkit-transform: translate(0em, 0.75em);
			          transform: translate(0em, 0.75em);
  }
  
  button.big-button:active::before {
	-webkit-transform: translate3d(0, 0, -1em);
	        transform: translate3d(0, 0, -1em);
	
		box-shadow: 0 0 0 2px rgb(58, 58, 58), 0 0.25em 0 0 rgb(58, 58, 58);
	
		box-shadow: 0 0 0 2px var(--colorShadeB), 0 0.25em 0 0 var(--colorShadeB);
  
  }

